import React, { useState, useContext } from 'react';
import {
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Box,
  IconButton,
  Tooltip,
  useMediaQuery
} from '@mui/material';
import {
  Person,
  FirstPage,
  LastPage,
  NavigateBefore,
  NavigateNext
} from '@mui/icons-material';
import { ThemeContext } from '../App';

function PlayerStats({ stats, searchPlayer, selectedDate }) {
  const { darkMode } = useContext(ThemeContext);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const isMobile = useMediaQuery('(max-width:600px)');

  const filteredStats = stats
    .filter(entry => {
      const matchesName = entry.player.toLowerCase().includes(searchPlayer.toLowerCase());
      const matchesDate = selectedDate === 'All Time' || entry.date === selectedDate;
      return matchesName && matchesDate;
    })
    .sort((a, b) => {
      const dateCompare = new Date(b.date) - new Date(a.date);
      if (dateCompare !== 0) return dateCompare;
      return b.kills - a.kills;
    });

  const totalPages = Math.ceil(filteredStats.length / rowsPerPage);
  const currentItems = filteredStats.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

  const handleFirstPage = () => setPage(0);
  const handlePreviousPage = () => setPage(prev => Math.max(prev - 1, 0));
  const handleNextPage = () => setPage(prev => Math.min(prev + 1, totalPages - 1));
  const handleLastPage = () => setPage(totalPages - 1);

  const noSelectStyle = {
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
    width: '100%',
    userSelect: 'none',
    WebkitUserSelect: 'none',
    MozUserSelect: 'none',
    msUserSelect: 'none',
    WebkitTapHighlightColor: 'transparent'
  };

  return (
    <Card elevation={3} sx={{ height: '100%' }}>
      <CardContent>
        <Box display="flex" alignItems="center" mb={2}>
          <Person color="primary" sx={{ mr: 1 }} />
          <Typography variant="h6" component="h2" sx={{ fontSize: '1rem' }}>
            Тоглогчийн статистик
          </Typography>
        </Box>

        <TableContainer>
          <Table size="small" sx={{ tableLayout: 'fixed' }}>
            <TableHead>
              <TableRow>
                <TableCell sx={{ fontWeight: 'bold', py: 1, width: '15%', fontSize: '0.75rem' }}>Огноо</TableCell>
                <TableCell sx={{ fontWeight: 'bold', py: 1, width: '20%', fontSize: '0.75rem' }}>Тоглогч</TableCell>
                <TableCell sx={{ fontWeight: 'bold', py: 1, width: '10%', textAlign: 'center', fontSize: '0.75rem' }}>Алуур</TableCell>
                <TableCell sx={{ fontWeight: 'bold', py: 1, width: '20%', fontSize: '0.75rem' }}>Өстөн дайсан</TableCell>
                <TableCell sx={{ fontWeight: 'bold', py: 1, width: '25%', fontSize: '0.75rem' }}>Хохирогч</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {currentItems.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={6} align="center" sx={{ py: 1, fontSize: '0.75rem' }}>
                    Үр дүн олдсонгүй.
                  </TableCell>
                </TableRow>
              ) : (
                currentItems.map((entry, i) => (
                  <TableRow key={i} hover>
                    <TableCell sx={{ py: 1, fontSize: '0.75rem' }}>{entry.date}</TableCell>

                    <TableCell sx={{ py: 1, fontSize: '0.75rem' }}>
                      <Tooltip title={entry.player} placement="top-start" enterTouchDelay={0} leaveTouchDelay={3000}>
                        <Box sx={noSelectStyle}>{entry.player}</Box>
                      </Tooltip>
                    </TableCell>

                    <TableCell sx={{ py: 1, textAlign: 'center' }}>
                      <Box
                        sx={{
                          display: 'inline-flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          width: isMobile ? 20 : 24,
                          height: isMobile ? 20 : 24,
                          borderRadius: '50%',
                          backgroundColor: 'error.main',
                          color: 'white',
                          fontSize: isMobile ? '0.6rem' : '0.7rem',
                          fontWeight: 'bold'
                        }}
                      >
                        {entry.kills}
                      </Box>
                    </TableCell>

                    <TableCell sx={{ py: 1, fontSize: '0.75rem' }}>
                      <Tooltip title={entry.nemesis} placement="top-start" enterTouchDelay={0} leaveTouchDelay={3000}>
                        <Box sx={noSelectStyle}>{entry.nemesis}</Box>
                      </Tooltip>
                    </TableCell>

                    <TableCell sx={{ py: 1, fontSize: '0.75rem' }}>
                      <Tooltip title={entry.victim} placement="top-start" enterTouchDelay={0} leaveTouchDelay={3000}>
                        <Box sx={noSelectStyle}>{entry.victim}</Box>
                      </Tooltip>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>

        <Box display="flex" justifyContent="space-between" alignItems="center" mt={2}>
          <Box>
            <IconButton onClick={handleFirstPage} disabled={page === 0} size="small">
              <FirstPage fontSize="small" />
            </IconButton>
            <IconButton onClick={handlePreviousPage} disabled={page === 0} size="small">
              <NavigateBefore fontSize="small" />
            </IconButton>
          </Box>
          <Typography variant="body2" sx={{ fontSize: '0.75rem' }}>
            {page + 1} / {totalPages}
          </Typography>
          <Box>
            <IconButton onClick={handleNextPage} disabled={page >= totalPages - 1} size="small">
              <NavigateNext fontSize="small" />
            </IconButton>
            <IconButton onClick={handleLastPage} disabled={page >= totalPages - 1} size="small">
              <LastPage fontSize="small" />
            </IconButton>
          </Box>
        </Box>
      </CardContent>
    </Card>
  );
}

export default PlayerStats;